html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

body {
  cursor: none;
  margin: 0;
  padding: 0;
  transition: background-color .3s, color .3s;
}

#scroll-container {
  -webkit-overflow-scrolling: touch;
  touch-action: pan-y;
  height: 100vh;
}

#cursor {
  pointer-events: none;
  z-index: 9999;
  background-color: #808080cc;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  transition: background-color .2s ease-in-out, transform .1s ease-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translate(-50%, -50%);
}

#cursor.hover {
  background-color: #b4b4b4e6;
  transform: translate(-50%, -50%)scale(1.5);
}

body.dark-mode #cursor {
  background-color: #b4b4b4e6;
}

body.dark-mode #cursor.hover {
  background-color: #808080cc;
}

*, :before, :after {
  box-sizing: border-box;
}

.container {
  box-sizing: border-box;
  max-width: 944px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 24px;
}

a {
  cursor: none;
  text-decoration: none;
}

a, a:visited, a:hover, a:active {
  color: inherit;
}

ul {
  align-items: center;
  gap: 16px;
  list-style-type: none;
  display: flex;
}

hr {
  border: 0;
  border-top: 1px solid #ccc;
  width: 100%;
  max-width: 100%;
  height: 1px;
  margin: 1em 0;
  padding: 0;
}

u {
  text-decoration-color: #ccc;
}

:root {
  color-scheme: light;
}

body.dark-mode {
  color-scheme: dark;
}

::selection {
  color: #000;
  background: #d3d3d3;
}

.mobile, .tablet {
  display: none;
}

.desktop {
  display: block;
}

.logo {
  filter: saturate(0%);
  transition: filter .2s ease-in-out;
}

.logo:hover {
  filter: saturate();
  transition: filter .2s ease-in-out;
}

.logo2 {
  opacity: .45;
  transition: opacity .2s ease-in-out;
}

.logo2:hover {
  opacity: 1;
  transition: opacity .2s ease-in-out;
}

.section-header {
  font-optical-sizing: auto;
  color: #00000080;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
}

.section-subheader {
  color: #0006;
  margin-top: 2px;
  font-size: 14px;
  font-weight: 500;
}

.tooltip {
  display: inline-block;
  position: relative;
}

.tooltip .tooltiptext {
  visibility: hidden;
  opacity: 0;
  color: #0006;
  text-align: center;
  z-index: 1;
  background-color: #fffc;
  border-style: solid;
  border-width: 1px;
  border-radius: 6px;
  width: 120px;
  padding: 5px 0;
  transition: opacity .2s ease-in-out;
  position: absolute;
}

.tooltiptext {
  font-optical-sizing: auto;
  font-family: Roboto Mono, monospace;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
  transition: opacity .2s ease-in-out;
}

@keyframes fadeInOpacity {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.fade-in1 {
  opacity: 1;
  animation-name: fadeInOpacity;
  animation-duration: 1s;
  animation-timing-function: ease-in;
  animation-iteration-count: 1;
}

.fade-in2 {
  opacity: 1;
  animation-name: fadeInOpacity;
  animation-duration: 1.5s;
  animation-timing-function: ease-in;
  animation-iteration-count: 1;
}

.fade-in3 {
  opacity: 1;
  animation-name: fadeInOpacity;
  animation-duration: 2s;
  animation-timing-function: ease-in;
  animation-iteration-count: 1;
}

.fade-in4 {
  opacity: 1;
  animation-name: fadeInOpacity;
  animation-duration: 2.5s;
  animation-timing-function: ease-in;
  animation-iteration-count: 1;
}

.fade-in5 {
  opacity: 1;
  animation-name: fadeInOpacity;
  animation-duration: 3s;
  animation-timing-function: ease-in;
  animation-iteration-count: 1;
}

.fade-in6 {
  opacity: 1;
  animation-name: fadeInOpacity;
  animation-duration: 3.5s;
  animation-timing-function: ease-in;
  animation-iteration-count: 1;
}

@media (prefers-color-scheme: dark) {
  body {
    color: #e0e0e0;
    background-color: #121212;
  }

  hr {
    border-top: 1px solid #444;
  }

  u {
    text-decoration-color: #666;
  }

  .tip {
    color: #ffffffb3;
  }

  .section-header, .ualberta, .project-header {
    color: #ccc;
  }

  .section-subheader, .degree, .header-subtext, .header-bottomtext, .project-subheader {
    color: #aaa;
  }

  .year, .project-year {
    color: #999;
  }

  .header-text {
    color: #ccc;
  }

  .tooltip .tooltiptext {
    color: #ccc;
    background-color: #212121e6;
    border-color: #666;
  }

  .projects img {
    border-color: #444;
  }

  .logo, .logo2 {
    filter: brightness(0) invert();
  }

  .logo:hover, .logo2:hover {
    filter: brightness(1.1) saturate();
  }

  .club {
    color: #ffffff80;
  }
}

body.dark-mode {
  color: #e0e0e0;
  background-color: #121212;
}

body.dark-mode hr {
  border-top: 1px solid #444;
}

body.dark-mode u {
