}

body.dark-mode .section-header, body.dark-mode .ualberta, body.dark-mode .project-header, body.dark-mode .header-text {
  color: #ccc;
}

body.dark-mode .section-subheader, body.dark-mode .degree, body.dark-mode .header-subtext, body.dark-mode .header-bottomtext, body.dark-mode .project-subheader {
  color: #aaa;
}

body.dark-mode .year, body.dark-mode .project-year {
  color: #999;
}

body.dark-mode .tooltip .tooltiptext {
  color: #ccc;
  background-color: #212121e6;
  border-color: #666;
}

body.dark-mode .projects img {
  border-color: #444;
}

body.dark-mode .logo, body.dark-mode .logo2 {
  filter: brightness(0) invert();
}

body.dark-mode .logo:hover, body.dark-mode .logo2:hover {
  filter: brightness(1.1) saturate();
}

body.dark-mode .tip, body.dark-mode .club {
  color: #ffffff80;
}

body:not(.dark-mode) {
  color: #000;
  background-color: #fff;
}

body:not(.dark-mode) hr {
  border: 0;
  border-top: 1px solid #ccc;
}

body:not(.dark-mode) u {
  text-decoration-color: #ccc;
}

body:not(.dark-mode) .tooltip .tooltiptext {
  color: #0006;
  background-color: #fffc;
  border-color: #ccc;
}

body:not(.dark-mode) .section-header {
  color: #00000080;
}

body:not(.dark-mode) .section-subheader {
  color: #0006;
}

body:not(.dark-mode) .logo, body:not(.dark-mode) .logo2 {
  filter: saturate(0%) !important;
}

body:not(.dark-mode) .logo:hover {
  filter: saturate() !important;
}

@media (prefers-reduced-motion: reduce) {
  .fade-in1, .fade-in2, .fade-in3, .fade-in4, .fade-in5, .fade-in6 {
    animation: none !important;
  }
}

@media (width <= 1024px) {
  #cursor {
    display: none;
  }

  .container {
    max-width: 100%;
    padding: 0 24px;
  }

  .mobile {
    display: none;
  }

  .tablet {
    display: block;
  }

  .desktop {
    display: none;
  }
}

@media (width <= 640px) {
  ul {
    margin-right: auto;
  }

  .mobile {
